<template>
    <el-dialog class="add-org-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="orgRules" label-width="120px">
            <el-row type="flex" :gutter="15">
              <el-col :span="8">
                <el-form-item label="组织名称" prop="name">
                    <el-input v-model="dialogForm.name" placeholder="请填写组织名称"></el-input>
                </el-form-item>
                <el-form-item label="业务指导部门" prop="businessDep">
                    <el-input v-model="dialogForm.businessDep" placeholder="请填写业务指导部门"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="组织类型" prop="type">
                    <el-select placeholder="请选择组织类型" clearable v-model="dialogForm.type">
                        <el-option v-for="(item, index) in dic['组织类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="人员数量" prop="personCount">
                    <el-input v-model="dialogForm.personCount" :maxlength="6" placeholder="请填写人员数量"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="组织层级" prop="hierarchy">
                    <el-select placeholder="请选择组织层级" clearable v-model="dialogForm.hierarchy">
                        <el-option v-for="(item, index) in dic['机构层级']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row type="flex" :gutter="15">
              <el-col :span="24">
                  <el-form-item label="主要职责" prop="mainFunction">
                    <el-input v-model="dialogForm.mainFunction" type="textarea" :rows="5" placeholder="请填写主要职责"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
        </el-form>
        <techo-position ref="position" :code="dialogForm.districtCode" :placement="'right'" @formLonLat="getLonlat"></techo-position>
        <div class="lon-lat">
            <span>经度：</span>
            <span>{{ dialogForm.x }}</span>
            <span>经度：</span>
            <span>{{ dialogForm.y }}</span>
        </div>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dialogForm: {
        x: "",
        y: "",
        districtCode: ""
      },
      orgRules: {
        name: [
          {
            required: true,
            message: "请输入群防群治组织名称",
            trigger: "blur"
          },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        type: [
          {
            required: true,
            message: "请选择群防群治组织类型",
            trigger: "change"
          }
        ],
        hierarchy: [
          {
            required: true,
            message: "请选择群防群治组织层级",
            trigger: "change"
          }
        ],
        businessDep: [
          { required: true, message: "请输入业务指导部门", trigger: "blur" },
          { min: 2, max: 50, message: "长度在 2 到 50 个字符", trigger: "blur" }
        ],
        personCount: [util.validate.valiInteger()],
        mainFunction: [
          { required: true, message: "请输入主要职责", trigger: "blur" },
          {
            min: 2,
            max: 1000,
            message: "长度在 2 到 1000 个字符",
            trigger: "blur"
          }
        ]
      },
      dic: {
        组织类型: [],
        机构层级: []
      }
    };
  },
  watch: {
    isAdd(value) {
      if (value == false) {
        this.$nextTick(() => {
          this.$refs["position"].addMarker({
            lng: this.dialogForm.x,
            lat: this.dialogForm.y
          });
        });
      }
    }
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  methods: {
    getLonlat(value) {
      this.dialogForm.x = value.lng;
      this.dialogForm.y = value.lat;
    },
    /** 保存 */
    submitForm() {
      if (this.dialogForm.x == undefined || this.dialogForm.x == "") {
        this.$popup.showNotification({
          type: "warning",
          message: "请选择地理位置！"
        });
      } else {
        this.$refs["dialogForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.dialogForm);
            param.dicMap && delete param.dicMap;
            if (this.isAdd == true) {
              // 添加
              service.requestSaveOrgMass(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "群防群治组织添加成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            } else if (this.isAdd == false) {
              // 修改
              service.requestUpdateOrgMass(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "群防群治组织修改成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.$refs["position"].mapApi.clear();
      this.$refs["position"].mapApi.mapOper.setFullExtent();
      this.dialogForm = {
        x: "",
        y: "",
        districtCode: ""
      };
    }
  }
};
</script>
<style>
.el-date-editor.el-input {
  width: 100%;
}
.add-org-dialog .lon-lat {
  display: inline-block;
  padding: 0 10px;
  font-size: 16px;
}
.add-org-dialog .lon-lat span {
  padding-right: 10px;
}
</style>